





















import Vue from 'vue'
export default Vue.extend({
  props: {
    keys: {
      type: Array,
      default () {
        return []
      }
    },
    info: {
      type: Object,
      default () {
        return {}
      }
    },
    image: {
      type: String,
      default: undefined
    },
    loadingText: {
      type: String,
      default: 'Loading...'
    }
  },
  methods: {
    getProperty (key: string, value: any, defaults: any): any {
      return key in value ? value[key] : defaults
    }
  }
})
