
























































import Vue from 'vue'

export declare type NewsPicture = {
  imgSrc: string,
  imgWidth: number,
  imgHeight: number,
  imgSize: number,
  imgTag?: any,
}

export declare type News = {
  id: string,
  content: string,
  emojiInfo?: any,
  pictures?: NewsPicture[],
  updateTime: string
}

export default Vue.extend({
  props: {
    news: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      modal: {},
      pictureModal: {}
    }
  },
  methods: {
    getLink (news: News) {
      const url = new URL(news.id, 'https://t.bilibili.com/')
      return url.toString()
    }
  }
})
